@import url(https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,400;0,500;1,400;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    font-family: 'Karla', sans-serif;
  }

header {
    position: fixed; /* pour fixer le header pendant le scroll */
    width: 100%;
    background-color: rgb(58, 118, 135);
    color: whitesmoke;
}

header > nav > ul {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    list-style: none;
    -webkit-justify-content : space-between;
            justify-content : space-between;
    padding : 0 2rem 0 2rem;
    background-color: rgb(58, 118, 135);
    color: whitesmoke;
}

footer {
    /* position: fixed; */
    background-color: thistle;
}

#wrapper {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-content: stretch;
            align-content: stretch;
    /* je voudrais que mon header s'éloigne du reste mais n'y arrive pas pour l'instant */
}

.HeaderNav {
    display: -webkit-flex;
    display: flex; 
    -webkit-justify-content: space-around; 
            justify-content: space-around;
    -webkit-flex-direction: row;
            flex-direction: row;
  }

#wrapper > main {
    display: -webkit-flex;
    display: flex;
    padding: 1rem;
    padding-top: 6rem;
    -webkit-justify-content: stretch;
            justify-content: stretch;
    -webkit-align-content: stretch;
            align-content: stretch;
    /* je voudrais que mon header s'éloigne du reste mais n'y arrive pas pour l'instant */
}
